import React from 'react';

const Birds = ({
	peeps,
	FaChevronCircleLeft,
	FaChevronCircleRight,
	index,
	right,
	left,
}) => {
	return (
		<div className="Birds">
			<h1>Birds of New Whittington</h1>
			<div className="profile">
				<h3>{peeps[index].name}</h3>
				<div className="image-icons">
					<FaChevronCircleLeft onClick={left} />
					<img src={peeps[index].img} alt="" />
					<FaChevronCircleRight onClick={right} />
				</div>
			</div>
		</div>
	);
};

export default Birds;
