import React from 'react';

const Nav = ({ tx, setIndex }) => {
	return (
		<div className={tx ? 'Nav a' : 'Nav'}>
			<h1>Nav</h1>
			<ul>
				<li onClick={() => setIndex(0)}>shadow bird</li>
				<li onClick={() => setIndex(1)}>Yellow/Black bird</li>
				<li onClick={() => setIndex(2)}>eagle</li>
				<li onClick={() => setIndex(3)}>Big beak bird</li>
				<li onClick={() => setIndex(4)}>blue bird</li>
			</ul>
		</div>
	);
};

export default Nav;
